<template>
	<div>
		<section class="hero hero--course">
			<div class="container">
				<div class="hero__wrapper hero__wrapper--centered">
					<h2 class="hero__primary">Getting Better with your life</h2>
					<p class="hero__secondary pt-20">Courses found on our platform</p>
				</div>  
			</div>
		</section>

		<section class="section">
			<h3 class="section__title">Recommended Courses</h3>
			<h6 class="section__secondary sub-title">The courses below are recommended based on your career choice.</h6>
			<div class="container">
				<template v-if="loadingClasses">
					<div class="loading">
						<loader />
					</div>
				</template>
				<template v-else>
					<template v-if="classesLoaded && classes.length">
						<div class="features__grid">
							<div class="features__grid__item"
							v-for="classItem in classes"
							:key="classItem.id">
								<div class="features__img">
									<img :src="classItem.avatar" alt="">
								</div>
								<div class="features__body">
									<h5 class="bold">
										{{classItem.title}}
									</h5>
									<p class="text" v-html="classItem.description"></p>
									<router-link
										:to="{ 
											name: 'course-detail', 
											params: { id: classItem.id }}">
										<button class="btn__primary btn__primary--wide">
											Start Learning
										</button>
									</router-link>
								</div>
							</div>
						</div>
						<div class="features__footer">
							<Pagination 
								v-if="total > limit"
								:options="{ limit, offset, total, baseUrl }"/>
						</div>
					</template>
					<template v-else-if="classesLoaded">
						<br/>
						<div class="alert alert-warning center" role="alert">
							<strong>Warning!</strong> No course available for your career choice.
						</div>
					</template>
					<template v-else>
						Error loading classes
					</template>
				</template>
			</div>
		</section>
	</div>
</template>

<script>
import { mapGetters } from 'vuex';
import Loader from '../../components/Loader';
import Pagination from '../../components/Pagination';
import { getFilteredCourses, getCourses } from '../../utils/api/course';
import { success } from '../../utils/toast';

export default {
	name: 'Index',
	components: {
		Loader,
		Pagination
	},
	data() {
		return {
			classes: [],
			loadingClasses: true,
			classesLoaded: false,
			limit: 6,
			offset: 0,
			total: 0,
			baseUrl: 'courses',
			status: 1
		};
	},
	mounted() {
		if (this.userObject.is_credential_uploaded !== 1) {
			success({
				title: 'Notice!',
				text: 'Kindly update your credentials to proceed, you are being redirected.',
				timer: 8000
			});
			setTimeout(() => {
				this.$router.push({ name: 'user-profile', params: { id: this.$session.get('id') } });
			}, 5000);
		} else if (this.userObject.is_psychometric_completed !== 1) {
			success({
				title: 'Notice!',
				text: 'Kindly complete your psychometric tests to continue, you are being redirected.',
				timer: 8000
			});
			setTimeout(() => {
				this.$router.push({ name: 'test' });
			}, 5000);
		}
	},
	created() {
		this.getCourses();
		this.limit = this.$route.query.limit ? this.$route.query.limit : this.limit;
		this.offset = this.$route.query.offset ? this.$route.query.offset : this.offset;
	},
	computed: {
		...mapGetters([
			'userObject'
		])
	},
	methods: {
		getFilteredCourses() {
			getFilteredCourses(this.status, this.userObject.career.career_id)
				.then((response) => {
					if (response.status === 200) {
						this.classes = response.data.data;
						this.classesLoaded = true;
						return true;
					}
					this.classesLoaded = false;
					return false;
				})
				.catch((error) => {
					this.classesLoaded = true;
					return error;
				})
				.finally(() => {
					this.loadingClasses = false;
				});
			return true;
		},
		getCourses() {
			getCourses()
				.then((response) => {
					if (response.status === 200) {
						this.classes = response.data.data;
						this.classesLoaded = true;
						return true;
					}
					this.classesLoaded = false;
					return false;
				})
				.catch((error) => {
					this.classesLoaded = true;
					return error;
				})
				.finally(() => {
					this.loadingClasses = false;
				});
			return true;
		}
	}
};
</script>

