	&__img {
		img {
			min-height: 165px;
			max-height: 165px;
			object-fit: cover;
			object-position: 50% 0%;
		}
	}
	&__body {
		.text {
			min-height: 100px;
		}
	}
}
.loading {
	display: flex;
	justify-content: center;
	align-items: center;
	height: 100vh;
}
.center {
	text-align: center;
}
